import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router";
import { FaBook, FaCalendar, FaColumns, FaPlus } from "react-icons/fa";
import { toast } from "react-toastify";
import { 
  Button, Form, FormGroup,
  Input, InputGroup, InputGroupAddon, InputGroupText, 
  Label 
} from "reactstrap";
import { Formik } from 'formik';

import "./SyllabusGenerate.css";

import { RoutesString } from "pages/routesString";
import subjectAPI from "api/common/subject";
import schoolYearAPI from "api/common/school-year";
import trainingTypeAPI from "api/common/training-type";
import syllabusAPI from "api/common/syllabus";
import LoadingBar from "components/common/LoadingBar/LoadingBar";

export default function SyllabusGenerate() {
  const { t } = useTranslation();

  const history = useHistory();

  const [initialValues, setInitialValues] = useState(
    {
      monHoc: '',
      namHoc: '',
      heDaoTao: ''
    }
  );
  const [isLoading, setIsLoading] = useState(false);
  const [subjects, setSubjects] = useState([]);
  const [schoolYears, setSchoolYears] = useState([]);
  const [trainingTypes, setTrainingTypes] = useState([]);

  useEffect(async () => {
    setIsLoading(true);
    try {
      const subjectRes = await subjectAPI.getAll();
      const schoolYearRes = await schoolYearAPI.getAll();
      const trainingTypeRes = await trainingTypeAPI.getAll();
      setSubjects(subjectRes.contents);
      setSchoolYears(schoolYearRes.contents);
      setTrainingTypes(trainingTypeRes.contents);
      setInitialValues({
        monHoc: subjectRes.contents[0].id,
        namHoc: schoolYearRes.contents[0].id,
        heDaoTao: trainingTypeRes.contents[0].id
      });
      // eslint-disable-next-line
    } catch(err) {
    } finally {
      setIsLoading(false);
    }
  }, []);

  const handleGenerateOnClick = async function(values) {
    setIsLoading(true);
    try {
      const body = {
        monHoc: parseInt(values.monHoc),
        namHoc: parseInt(values.namHoc),
        heDaoTao: parseInt(values.heDaoTao),
        moTa: "",
        taiNguyen: "",
        quiDinh: ""
      };
      const res = await syllabusAPI.post(body);
      history.push(`${RoutesString.ADMIN_SYLLABUS_UPDATE}?id=${res.id}`);
    } catch(err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
        else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
        } else {
            toast.error(t("RESPONSE_MESSAGE500"));
        }
    }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <div className="page-content">
        <Formik initialValues={initialValues}
        enableReinitialize={true}>
          {({values, handleChange}) => (
            <Form>
              <FormGroup className="row">
                <Label for="monHoc" className="col-lg-3">
                  {t("admin.syllabus_generate.SUBJECT")}
                </Label>
                <div className="col-lg-9">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText><FaBook /></InputGroupText>
                    </InputGroupAddon>
                    <Input type="select" 
                    name="monHoc"
                    value={values.monHoc}
                    onChange={handleChange}>
                      { subjects && subjects.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.tenTiengViet}
                            </option>
                          )
                        })
                      }
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup> <br />
              <FormGroup className="row">
                <Label for="namHoc" className="col-lg-3">
                  {t("admin.syllabus_generate.SCHOOL_YEAR")}
                </Label>
                <div className="col-lg-9">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText><FaCalendar /></InputGroupText>
                    </InputGroupAddon>
                    <Input type="select" 
                    name="namHoc"
                    value={values.namHoc}
                    onChange={handleChange}>
                      { schoolYears && schoolYears.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.ten}
                            </option>
                          )
                        })
                      }
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup> <br />
              <FormGroup className="row">
                <Label for="heDaoTao" className="col-lg-3">
                  {t("admin.syllabus_generate.TRAINING_TYPE")}
                </Label>
                <div className="col-lg-9">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText><FaColumns /></InputGroupText>
                    </InputGroupAddon>
                    <Input type="select" 
                    name="heDaoTao"
                    value={values.heDaoTao}
                    onChange={handleChange}>
                      { trainingTypes && trainingTypes.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.ten}
                            </option>
                          )
                        })
                      }
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup> <br />
              <FormGroup>
                <Button color="success"
                onClick={() => handleGenerateOnClick(values)}>
                  <FaPlus /> {t("admin.common.GENERATE")}
                </Button>
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
